import React from "react";

import {
  Card,
  CardActionArea,
  // CardContent,
  CardMedia,
  // Typography,
} from "@material-ui/core";

import "./App.css";

export default function TextCard({
  width,
  height,
  imgHeight,
  hatch,
  backgroundHeight,
}) {
  const renderVideo = () => {
    console.log(hatch.backgroundVideo);
    return (
      <CardActionArea>
        <CardMedia
          style={{ height: imgHeight }}
          image={hatch.type === "video" ? hatch.backgroundVideo : hatch.img}
          // image={hatch.backgroundVideo}
        />
        {/* <CardContent>
          <Typography
            variant={width === 250 ? "body2" : "h6"}
            // variant="body2"
            color="textSecondary"
            component="p"
            paragraph={width === 250 ? false : true}
          >
            {hatch.title_en}
          </Typography>
          <Typography
            variant={width === 250 ? "body2" : "h6"}
            // variant="body2"
            color="textSecondary"
            component="p"
          >
            {hatch.title_de}
          </Typography>
        </CardContent> */}
      </CardActionArea>
    );
  };

  const renderText = () => {
    return (
      <CardActionArea>
        <CardMedia
          style={{ height: backgroundHeight }}
          image={hatch.backgroundText}
        >
          {/* <div className="bibleVerse">{hatch.bibleVerse_en}</div>
          <div className="bibleContent">{hatch.bibleVerse_de}</div> */}
        </CardMedia>
      </CardActionArea>
    );
  };

  return (
    <Card style={{ width: width, height: height }}>
      {hatch.type === "video" ? renderVideo() : renderText()}
    </Card>
  );
}
