import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import MuiDialogContent from "@material-ui/core/DialogContent";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

export default function ErrorDialog({
  openError,
  handleErrorDialogClose,
  selected,
}) {
  const getOrdinalNum = (number) => {
    let selector;

    if (number <= 0) {
      selector = 4;
    } else if ((number > 3 && number < 21) || number % 10 > 3) {
      selector = 0;
    } else {
      selector = number % 10;
    }

    return ["th", "st", "nd", "rd", ""][selector];
  };

  return (
    <Dialog
      onClose={handleErrorDialogClose}
      aria-labelledby="customized-dialog-title"
      open={openError}
    >
      <DialogTitle
        id="customized-dialog-title"
        onClose={handleErrorDialogClose}
      >
        Oops! / Hoppla!
      </DialogTitle>
      <DialogContent dividers>
        <Typography gutterBottom>
          The content can’t be viewed yet! Please come back on the {selected.nr}
          <sup>{`${getOrdinalNum(selected.nr)}`}</sup> of December 2020 to open
          content number {selected.nr}
        </Typography>
        <Typography gutterBottom>
          Der Inhalt kann noch nicht angesehen werden! Bitte schauen Sie am{" "}
          {selected.nr}. Dezember 2020 wieder vorbei, um den Inhalt Nummer{" "}
          {selected.nr} zu öffnen.
        </Typography>
      </DialogContent>
    </Dialog>
  );
}
